@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');
:root{
  --backgound: #1A1A1A;
  --white: #fafafa;
  --font: 'Montserrat';
  --green: #17b802;
  --green-gradient: linear-gradient(90deg, rgba(150,255,136,1) 0%, rgba(23,184,2,1) 35%, rgba(99,254,79,1) 100%);
}
body{
  margin: 0;
  font-family: var(--font);
  background-color: #fafafa;
}
.nav-buttons{
  list-style: none;
  display: flex;
  margin: 1em 3em;
  padding: 0;
}

header{
  background-color: var(--backgound);
  margin: 0;
  color: #fafafa;
}

header nav{
  display: flex;
  justify-content: space-between;
}
.logo{
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 10px 1em;
  cursor: pointer;
  font-size: 1.4rem;
  font-weight: 900;
  height: 60px;
}
.logo a{
  height: 100%;
}
.logo img{
  height: 100%;
}
/* .active-page{
  border-bottom: 2px solid ;
  color:YOUR-LINK-COLOR;
  padding-bottom:1px;
} */
.nav-buttons li {
  transition: color 0.1s, background-color 0.1s;
  position: relative;
  display: block;
  cursor: pointer;
  margin: 0 12px;
  font-size: 1rem;
  line-height: 16px;
  white-space: nowrap;
  font-weight: 500;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: color 0.1s,background-color 0.1s,padding 0.2s ease-in;
}
.nav-buttons li::before {
  content: '';
  display: block;
  position: absolute;
  bottom: 3px;
  left: 0;
  height: 3px;
  width: 100%;
  background-color: var(--green);
  transform-origin: right top;
  transform: scale(0, 1);
  transition: color 0.1s,transform 0.2s ease-out;
}
.nav-buttons li:active::before {
  background-color: var(--green);
}
.nav-buttons li:hover::before, a:focus::before {
  transform-origin: left top;
  transform: scale(1, 1);
}
.intro-section{
  height: 95vh;
  width: 100%;
  background-color: var(--backgound);
  padding: 0;
  display: flex;
  justify-content: center;
  margin: 0;
  background-image: url(./images/gribanica.png);
  background-repeat: no-repeat;
  background-position: 50% 200%;
  background-size: 90vh;
  clip-path: polygon(0% 0%, 100% 0%, 100% 90%, 0% 100%);
  /* background-attachment: fixed; */
}
.intro-section::before {
  content: "";
  background-image: url(./images/gribanica.png);
  position: fixed;
  z-index: -1;
  top: 0;
  background-position: center;
  background-repeat: no-repeat;
  /* background-size: cover; */
}
.box {
  animation-duration: 2s;
  animation-iteration-count: infinite;
  /* transform-origin: bottom; */
}
.bounce-2 {
  animation: bounce-2 2.5s ease-in-out infinite;
}
.bounce-3{
  animation: bounce-3 4s infinite;
}
.arrow{
  position: absolute;
  bottom: 0px;
  right: 50%;
  scale: 2;
  /* transform: rotate(90deg); */
  translate: 50%;
  filter: invert(1);
}
@keyframes bounce-2 {
  0%   { transform: translateY(0); }
  50%  { transform: translateY(-80px); }
  100% { transform: translateY(0); }
}
@keyframes bounce-3 {
  0%   { transform: translateY(0); }
  50%  { transform: translateY(-20px); }
  100% { transform: translateY(0); }
}
.about-me{
  width: 80%;
  margin: 3em 10%;
  height: 100%;
  background-color: var(--white);
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
.developer{
  color: var(--green);
  margin: auto 1rem;
  max-width: 50%;
  /* min-width: 20em; */
}
.about{
  min-height: 10em;
}
.about h2{
  display: inline-block;
}
h2 span{
  white-space: nowrap;
}
@keyframes slide-in {
  0% {
    transform: translateX(100%);
  }
  100% {
    transform: translateX(0);
  }
}
.slide-in{
  animation: slide-in 1s ease-out;
}

.quote{
  background-color: var(--backgound);
  color: var(--white);
  padding: 2rem;
  max-width: 50%;
  display: flex;
  flex-wrap: wrap;
  min-width: 250px;
  align-items: center;
  font-size: 1.1em;
  min-height: 12rem;
  margin: 0 2rem;
  
}
.quote blockquote{
  font-weight: 400;
  font-style: italic;
  margin: 25px 40px;
  text-align: center;
}
blockquote span{
  color: var(--green);
  font-weight: 500;
  float: right;
  margin: 10px 10px 0;
}

.timline-section{
  margin: 5em 10%;
}
.timline-section h2, .projects h2, footer h2, .skills h2{
  color: var(--green);
  font-weight: 700;
  font-size: 2.2em;
}
.timeline{
  display: flex;
  /* flex-wrap: wrap; */
  width: 100%;
  height: 100%;
  margin: 0;
  justify-content: center;
}
.line{
  min-height: 100%;
  /* min-height: 28em; */
  width: 5px;
  border-radius: 5px;
  background-color: var(--backgound);
  margin: 30px 0 0 0;
}
.timeline-ul{
  padding: 0;
}
.pika{
  height: 15px;
  width: 15px;
  background-color: var(--green);
  border-radius: 100%;
  margin: 0 0 0 -10px;
}
.timeline-ul li{
  margin-bottom: 2.5em;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  font-size: 1.1em;
  align-items: center;
  padding: 10px 0;
  min-width: 10em;
}
.informacija{
  width: 350px;
  margin-left: 35px;
}
.date{
  margin: 0 20px;
  width: 4em;
  text-align: center;
  font-weight: 700;
}
header a{
  text-decoration: none;
  color: var(--white);
  padding: .5rem 1.5rem;
}
.informacija a{
  text-decoration: none;
  color: var(--green);
  font-weight: 700;
  font-style: italic;
}
html{
  scroll-behavior: smooth;  
}
.projects{
  padding: 8em 10%;
  width: 80%;
  height: 100%;
  background-color: var(--backgound);

}
.gallery{
  margin-top: 4rem;
  display: flex;
  flex-wrap: wrap;
  
  justify-content: center;
  align-items: center;
}
.gallery img{
  max-height: 10em;
  max-width: 10em;
  margin: 2rem;
  filter: grayscale(1);
  transition: ease-in-out 0.15s;
}

.gallery img:hover{
  scale: 1.05;
}

footer{
  background-color: var(--white);
  width: 80%;
  margin: 2em 10% 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
footer h2{
  width: 100%;
}
.left-footer{
  width: clamp(250px, 40%, 600px);
  background-color: var(--white);
  padding: 2em;
  margin: 1em 0 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  opacity: 0.76;
}

.right-footer{
  width: clamp(250px, 40%, 600px);
  padding: 2em;
  border-radius: 0.3em;
  background-color: var(--backgound);
}
.contact-me{
  font-size: 1.4rem;
  font-weight: 900;
  margin: 0 0 0.8em;
  color: var(--white);
}

footer ul, .contact-form{
  margin: 0;
  padding: 0;
}

.bottom{
  margin: 0 0 20px;
  padding: 0;
  justify-content: left;
}

.bottom-links {
  list-style: none;
  text-transform: uppercase;
  margin:0 1em 0.3em;
  letter-spacing: 1px;
  cursor: pointer;
  text-decoration: underline ;
  color: var(--backgound);
}
.bottom-links a{
  color: var(--backgound);
}
.credit{
  width: 60%;
  margin: 3em 20% 0 ;
  color: #1a1a1a86;
  text-align: center;
  font-size: 0.9em;
}
form{
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  justify-content: space-between;
}
form input{
  border: none;
  background-color: var(--white);
  padding: 0.5em 1em;
  font-size: 1em;
  width: 100%;
  /* margin-right: 1em; */
  border-radius: 0;
  box-shadow: none;
  margin-bottom: 1.2em;
}
form textarea{
  padding: 0.5em 1em;
  /* margin-top: 1em; */
  min-height: 4em;
  font-weight: 500;
  font-size: 1em;
  width: 100%;
  font-family: var(--font);
  max-height: 10em;
  resize: vertical;
}
form button{
  border: none;
  background-color: var(--green);
  font-weight: 700;
  color: var(--white);
  padding: 0.5em 1em;
  font-size: 1em;
  width: 100%;
  /* margin-right: 1em; */
  border-radius: 0;
  box-shadow: none;
  cursor: pointer;
  margin-top: 1em;
}
footer span{
  display: flex;
  flex-wrap: wrap-reverse;
  align-items: center;
  justify-content: center;
}

.scroll-top{
  height: 4em;
  rotate: 90deg;
  cursor: pointer;
  /* margin: 0 2em ; */
}
.burger-btn{
  padding: 20px;
  display: none;
  flex-wrap: wrap;
  width: 2em;
}
.burger-btn span{
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  height: 4px;
  border-radius: 5px;
  width: 2em;
  margin-bottom: 6px;
  background-color: var(--white);
  z-index: 2;
}

.social{
  display: flex;
  width: 100%;
  padding: 1.2em 0 0em 0;
  justify-content: space-around;
  align-items: center;
}
.social ul{
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  width: 100%;
}
.social ul li{
  width: 15%;
  max-width: 34px;
}
.social ul li img{
 width: 100%;
 filter:invert(0.9);
 border-radius: 12px;
}
.nav-buttons li div{
  position: absolute;
  top: 7px;
  right: -2px;
  transform: rotate(30deg);
  font-size: 0.8rem;
  font-weight: 700;
  color: var(--green);
  animation: flashing 1.4s infinite ease-in-out;
}
@keyframes flashing{
  0%{
    opacity: .3;
  }
  50%{
    opacity: 1;
  }
  100%{
    opacity: .3;
  }
}

/* .closed{
  animation: slide-in 0.5s linear reverse;
  opacity: 0;
  z-index: -1;
} */
.opened li{
  margin: 0;
  width: 30%;
  padding: 2rem 10%;
  text-align: center;
}
@keyframes slide-in {
  0% {
    transform: translateY(-100%);
  }
  100% {
    transform: translateY(0);
  }
}
.skills{
  padding: 1em 10% 8em 10%;
  clip-path: polygon(0% 0%, 100% 0%, 100% 90%, 0% 100%);
}
.skill-container{
  margin: 0 10%;
  width: 80%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;

}
.bubbles{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 2rem 0;
}
.skill-bubble{
  min-width: 100px;
  padding: .5rem;
  height: 80px;
  border-radius: 0.3em;
  background-color: var(--green);
  margin: 1em;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1em;
  color: var(--white);
  font-weight: 700;
  box-shadow: 0 0 10px rgba(0,0,0,0.2);
  transition: 0.2s ease-in-out;
  white-space: nowrap;
  cursor: pointer;
  animation: bubbling-effect 1.5s infinite;
}
.skill-bubble:nth-child(1), .skill-bubble:nth-child(6), .skill-bubble:nth-child(5), .skill-bubble:nth-child(8), .skill-bubble:nth-child(9), .skill-bubble:nth-child(12){
  animation-delay: 0.5s;
  margin: 1rem 1.5rem;
}
.skill-bubble:nth-child(3), .skill-bubble:nth-child(4), .skill-bubble:nth-child(6), .skill-bubble:nth-child(7), .skill-bubble:nth-child(11), .skill-bubble:nth-child(14){
  animation-delay: .82s;
  margin: 1rem 1.5rem;
}
.skill-bubble:hover{
  transform: scale(1.05);
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.13);
  animation: bubbling-effect 2s infinite;
}
@keyframes bubbling-effect{
    0% {
      transform: translateY(0) translateX(0);
    }
    50% {
      transform: translateY(-2px) translateX(1px);
    }
    100% {
      transform: translateY(0) translateX(0);
    }
  }
  @media (max-width:850px){
    .nav-buttons{
      display: none;
    }
    .quote blockquote{
      margin: 25px 15px;
  
    }
    .timline-section{
      margin: 5em 0 5em 5%;
    }
    .date{
      width: 100%;
      text-align: left;
      margin-bottom: 0.3em;
    }
    .informacija{
      width: 100%;
      font-size: 0.95em;
    }
    .intro-section{
      background-position: 50% 100% !important;
      background-size: 90vh !important;
    }
    .burger-btn{
      display: flex !important;
    }
    .right-footer, .left-footer{
      width: 100% !important;
    }
    .skill-container{
      width: 100% !important;
      margin: 0 !important;
    }
    .skill-bubble{
      margin: 0.8rem !important;
      padding: .3rem;
    }
    .opened{
      display: flex;
      flex-wrap: wrap;
      /* justify-content: center; */
      align-items: center;
      background-color: var(--backgound);
      position: fixed;
      top: 80px;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: 100;
      margin: 0;
      flex-direction: column;
      animation: slide-in 0.1s linear;
    }
  }
  .JqqAIg {
    height: auto !important;
  }